// Reducer for fetch profiles

// Import Action types
import {
	GET_TRUCKS,
	PROFILE_LOADING,
	CLEAR_CURRENT_PROFILE
} from "../actions/types";

const initialState = {
	profile: null,
	profiles: null,
	trucks: [],
	loading: false
};

export default function(state = initialState, action) {
	switch (action.type) {
		case PROFILE_LOADING:
			return {
				...state,
				loading: true
			};
		case GET_TRUCKS:
			return {
				...state,
				trucks: action.payload,
				loading: false
			};
		case CLEAR_CURRENT_PROFILE:
			return {
				...state,
				profile: null
			};
		default:
			return state;
	}
}
